
.top-bar {
  text-align: left;
  padding: 5px 0;
  /* background-color: #584b4f; */
  background: #2c2a2b;
  color: #efeae1;
  font-size: 14px;
  /* height: 33px; */
}
.wrapper {
  width: 960px;
  margin: 0 auto;
  overflow: hidden;
}
.top-bar navigation {
  display: inline-block;
}
.navigation {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.navigation li {
  display: inline-block;
  padding-right: 5px;
  width: 55px;
  vertical-align: top;
}
.navigation a {
  padding: 5px;
  display: inline-block;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}
.top-bar a {
  color: #efeae1;
  text-decoration: none;
}
.top-bar a:hover {
  color: #81c08b;
  text-decoration: none;
}

.section {
    position: relative;
    /* min-height: 600px; */
    overflow: hidden;
    text-align: center;
    background-image: radial-gradient(ellipse closest-side at 50% 50%, #393e44, #36383b 25%, #343233);
    background-repeat: no-repeat;
    background-size: 100% 1000px;
    background-position: center -120px;
}
.section .wrapper {
    padding-top: 50px;
    padding-bottom: 50px;
}
.hreo-header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.hero-logo {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 500px;
            flex: 0 0 500px;
    height: 366px;
    background-repeat: no-repeat;
    background-position: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.hero-header-item {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.hero-logo-circles {
    width: 366px;
    height: 366px;
    margin: auto;
    position: relative;
}
@-webkit-keyframes hero-logo-circle{
100%{
        -webkit-transform:rotate(1turn);
                transform:rotate(1turn)
}
}
@keyframes hero-logo-circle{
100%{
        -webkit-transform:rotate(1turn);
                transform:rotate(1turn)
}
}
@-webkit-keyframes hero-octonaut {
0% {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px)
}
100% {
        -webkit-transform: translateY(10px);
                transform: translateY(10px)
}
}
@keyframes hero-octonaut {
0% {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px)
}
100% {
        -webkit-transform: translateY(10px);
                transform: translateY(10px)
}
}
@-webkit-keyframes hero-octonaut2 {
0% {
        -webkit-transform: rotate(10deg);
                transform: rotate(10deg);
}
100% {
        -webkit-transform: rotate(-10deg);
                transform: rotate(-10deg);
}
}
@keyframes hero-octonaut2 {
0% {
        -webkit-transform: rotate(10deg);
                transform: rotate(10deg);
}
100% {
        -webkit-transform: rotate(-10deg);
                transform: rotate(-10deg);
}
}
.hero-logo-circle {
    position: absolute;
    left: 0;
    top: 0;
    -webkit-animation: hero-logo-circle 1s linear infinite;
            animation: hero-logo-circle 1s linear infinite;
    will-change: transform;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.hero-logo-circle:nth-child(1) {
    -webkit-animation-duration: 30s;
            animation-duration: 30s;
}
.hero-logo-circle:nth-child(2) {
    -webkit-animation-duration: 40s;
            animation-duration: 40s;
}
.hero-logo-circle:nth-child(3) {
    -webkit-animation-duration: 50s;
            animation-duration: 50s;
}
.hero-logo-circle:nth-child(4) {
    -webkit-animation-duration: 60s;
            animation-duration: 60s;
}
.hero-logo-circle:nth-child(5) {
    -webkit-animation-duration: 70s;
            animation-duration: 70s;
}
.hero-logo-circle:nth-child(6) {
    -webkit-animation-duration: 80s;
            animation-duration: 80s;
}
.hero-logo-circle:nth-child(7) {
    -webkit-animation-duration: 90s;
            animation-duration: 90s;
}
.hero-logo-circle:nth-child(8) {
    -webkit-animation-duration: 100s;
            animation-duration: 100s;
}
.hero-logo-circle:nth-child(9) {
    -webkit-animation-duration: 110s;
            animation-duration: 110s;
}
.hero-logo-circle:nth-child(10) {
    -webkit-animation-duration: 120s;
            animation-duration: 120s;
}
.hero-octonaut {
    position: absolute;
    top: 50px;
    left: calc(50% - 585px);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-animation: hero-octonaut 2s alternate ease-in-out infinite;
            animation: hero-octonaut 2s alternate ease-in-out infinite;
}
.hero-octonaut2 {
    z-index: 1;
    position: fixed;
    top: 10px;
    right: 18px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-animation: hero-octonaut2 3s alternate ease-in infinite;
            animation: hero-octonaut2 3s alternate ease-in infinite;
}
.article {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    list-style: none;
    padding: 0;
    margin: 0;
    margin-left: 30px;
}
.article li {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    padding: 0.9em 0;
    border-bottom: 1px solid #4e4b4d;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
}
.article li:nth-child(1) {
    color: #78b0a0;
    font-size: 1.1em;
}
.article li:last-child {
    color: #8d8273;
    font-size: 0.9em;
}
.article li:nth-child(1) span{
    background-image: -webkit-linear-gradient(left,#cddc39,#ff9800 25%,#cddc39 50%,#ff9800 75%,#cddc39)!important;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-size: 200% 100%;
}
.article li:nth-child(1) span:before{
    content: "";
    display: inline-block;
    position: relative;
    top: 2px;
    left: -2px;
    width: 13px;
    height: 15px;
    background: url(http://cdn.alloyteam.com/assets/img/hot-32d141.png) 50% no-repeat;
    background-size: 100% 100%;
}
.article li a {
    font-size: 1.0em;
    line-height: 2rem;
    letter-spacing: .10em;
    font-weight: 500;
    color: rgb(239, 218, 185);
    text-decoration: none;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}
.article li a:hover {
    color: #3F9EA9;
}
